  height: 150px;
  /* overflow: hidden; */
  width: 460px;
}

#phrases {
  -webkit-animation: slide-phrases-upward 20s;
  animation: slide-phrases-upward 20s;
}

#footer {
  bottom: 30px;
  color: white;
  display: flex;
  justify-content: center;
  left: 0;
  position: fixed;
  right: 0;
}

@-webkit-keyframes slide-phrases-upward {
  0% {
    transform: translateY(0px);
  }

  5% {
    transform: translateY(-50px);
